const _ = require('lodash');
export class Binding {
  constructor(el, tpl, expr) {
    this.el = el;
    this.tpl = tpl;
    this.target = tpl.bind;
    this.expr = expr;
  }

  bind() {
    throw 'Abstract Method [' + this.getDirectiveClassName() + '.bind]';
  }

  unbind() {
    throw 'Abstract Method [' + this.getDirectiveClassName() + '.unbind]';
  }

}



